import invalidRecordsModal from './invalidRecordsModal/index'
import outofscopeRecordsModal from './outOfScopeRecordsModal/index'

export default {
  components: {
    invalidRecordsModal,
    outofscopeRecordsModal,
  },
  data() {
    return {
      showInvalidDetails: false,
      showOutOfPeriodDetails: false,
    }
  },
  computed: {
    recordsCount() {
      return this.$store.getters.recordsCount
    },
    total() {
      return Object.keys(this.recordsCount).reduce((total, key) => (total + this.recordsCount[key]), 0)
    },
  },
}



