import importFPMMixin from '../../importFPMMixin'


export default {
  mixins: [importFPMMixin],
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    records() {
      return this.$store.getters.outOfScopeRecords
    },
  },
  methods: {
    load() {
      return this.$store.dispatch('loadOutOfScopeRecords')
    },
  },
}



