// TODO: validate fields
import Datepicker from 'vuejs-datepicker'
import {STATEMENT_TYPES} from '@/nomenclaturesConstants'


export default {
  components: {
    Datepicker,
  },
  data() {
    return {
      formData: {},
      statementTypes: STATEMENT_TYPES,
      isFileValid: null,
      errMsg: null,
      loading: false,
    }
  },
  methods: {
    onFileChange(event) {
      this.$validator.validate('file')
        .then(isValid => {
          // this.isFileValid = isValid
          // if (isValid) {
          //   this.fileData = fileUtils.filesChangeHandler(event)[0]
          // }
          // this.fileData = null
        })
    },
    submit() {
      const formData = this.formData

      if (!formData.statementType || !formData.toDate || !formData.file || (formData.statementType.id > 1 && !formData.fromDate)) {
        this.errMsg = 'Please, fill the required fields.'
        return
      }

      this.loading = true
      this.errMsg = null

      this.$store.dispatch('uploadExcel', this.formData)
        .then(() => {
          this.$emit('success')
        })
        .catch(errRes => {
          this.errMsg = this.$t(errRes.message)
        })
        .finally(() => {
          this.loading = false
        })
    },
  },
}



