import runAfterNomenclatureIsSetMixin from '../../runAfterNomenclatureIsSetMixin'


export default {
  mixins: [runAfterNomenclatureIsSetMixin],
  created() {
    this.$store.dispatch('loadCountries')
  },
  mounted() {
    this.loading = true
    this.runAfterNomenclatureIsSet('countries', () => {
      this.loading = false
    })
  },
  data() {
    return {
      formData: {},
      errMsg: null,
      loading: false,
    }
  },
  computed: {
    countries() {
      return this.$store.getters.countries
    },
  },
  methods: {
    validate() {
      return this.$validator.validateAll()
    },
    save() {
      return this.$store.dispatch('inviteContact', this.formData)
        .then((invitation) => {
          this.$emit('success', invitation)
        })
    },
    submit() {
      this.errMsg = null
      this.loading = true
      this.validate()
        .then(result => {
          // result comes from vee-validate
          if (result) {
            return this.save()
          }
          return Promise.reject(new Error('Please, fill the required fields.'))
        })
        .catch(errRes => {
          this.errMsg = this.$t(errRes.message)
        })
        .finally(() => {
          this.loading = false
        })
    },
  },
}



