import {ROLES} from '@/nomenclaturesConstants'


export default {
  name: 'CollaboratorsTableRow',
  props: {
    collaborator: {
      required: true,
    },
  },
  data() {
    return {
      model: Object.assign({}, this.collaborator),
      roles: ROLES,
      editMode: false,
      loading: false,
    }
  },
  methods: {
    save() {
      return this.$store.dispatch('updateCollaborator', this.model)
        .then(() => {

        })
    },
    edit() {
      this.editMode = true
    },
    cancel() {
      this.editMode = false
    },
    remove() {
      // TODO:
    },
  },
}



