<template>
  <div :class="['company-card', 'company-card-' + size]">
    <logo-flag :company="company" :size="size"></logo-flag>
    <div class="company-card-name">{{company.name}}</div>
  </div>
</template>


<script>
import LogoFlag from './LogoFlag'

export default {
  components: {
    LogoFlag,
  },
  props: {
    company: {
      required: true,
    },
    size: {
      required: true,
    },
  },
}
</script>



