import UploadForm from './UploadForm/index'
import RcTable from './RecognizedCompaniesTable/index'
import UcTable from './UnrecognizedCompaniesTable/index'
import SummaryTable from './Summary/index'


export default {
  name: 'UploadPage',
  components: {
    UploadForm,
    RcTable,
    UcTable,
    SummaryTable,
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    summary() {
      return this.$store.getters.importSummary
    },
  },
  methods: {
    onUpload() {
      this.loading = true
      this.$store.dispatch('loadSummary')
        .then(() => {
          this.loading = false
        })
    },
    cancel() {
      this.$store.commit('clearImport')
    },
  },
}



