// import tableTools from '@/components-lib/table-tools'
// import {options} from './CollaboratorsTable'
import runAfterSCisSetMixin from '../runAfterSCisSetMixin'
import CollaboratorForm from './CollaboratorForm/index'
import CollaboratorsTable from './CollaboratorsTable/index'


export default {
  components: {
    CollaboratorForm,
    CollaboratorsTable,
  },
  mixins: [runAfterSCisSetMixin],
  created() {
    this.runAfterSCisSet(this.loadCollaborators)
  },
  data() {
    return {
      showForm: false,
      loading: false,
    }
  },
  // computed: {
  //   collaborators() {
  //     return this.$store.getters.collaborators
  //   },
  // },
  methods: {
    loadCollaborators() {
      this.loading = true
      this.$store.dispatch('loadSCCollaborators')
        // .then((newCollaborators) => {
        //   if (newCollaborators) {
        //     tableTools.tableStore.commit('setItems', newCollaborators)
        //   }
        // })
        .finally(() => {
          this.loading = false
        })
    },
  },
}



