import ReceivedInvitationsTable from './ReceivedInvitationsTable/index'


export default {
  components: {
    ReceivedInvitationsTable,
  },
  created() {
    this.loading = true
    this.$store.dispatch('loadReceivedInvitations')
      .finally(() => {
        this.loading = false
      })
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    receivedInvitations() {
      return this.$store.getters.receivedInvitations
    },
  },
  methods: {
  },
}



