import axios from 'axios'
import {apiToUser, userToApi} from './converters/userConverters'

const ACCOUNT_API = '/Account/'

// TODO: photo

export default {
  getUser() {
    return axios.get(ACCOUNT_API)
      .then(apiToUser)
  },
  update(data) {
    // TODO: const postData = userToApi(formData)
    return axios.patch(ACCOUNT_API, data)
  },
  // AUTH
  register(formData) {
    const postData = userToApi(formData)
    return axios.post(ACCOUNT_API, postData)
      .then(apiToUser)
  },
  login: function({email, password}) {
    const requestConfig = {
      headers: {
        'Authorization': 'Basic ' + btoa(email + ':' + password),
      },
    }
    return axios.post(ACCOUNT_API + 'Login', null, requestConfig)
      .then(apiToUser)
  },
  logout() {
    return axios.get(ACCOUNT_API + 'Logout')
  },
  requestPassReset(email) {
    return axios.post(ACCOUNT_API + 'Recover', {email})
  },
  resetPass(token, password) {
    return axios.post(ACCOUNT_API + 'Recover/' + token, {password})
  },
}



