import tableTools from '@/components-lib/table-tools'
import CompanyCard from '@/components-app/CompanyCard'


export default {
  components: {
    CompanyCard,
  },
  directives: {
    Sort: tableTools.SortDirective(tableTools.tableStore),
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    pageItems() {
      tableTools.tableStore.commit('setItems', this.$store.getters.companySentInvitations)
      return tableTools.tableStore.getters.pageItems()
    },
  },
  methods: {
    cancel(invitation) {
      this.loading = true
      this.$store.dispatch('cancelInvitation', invitation)
        .finally(() => {
          this.loading = false
        })
    },
  },
}



