import tableTools from '@/components-lib/table-tools'

// TODO: upload()
// TODO: reconcile()
// TODO: addAlias()

export default {
  name: 'ContactsTable',
  directives: {
    Sort: tableTools.SortDirective(tableTools.tableStore),
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    pageItems() {
      tableTools.tableStore.commit('setItems', this.$store.getters.contacts)
      return tableTools.tableStore.getters.pageItems()
    },
  },
}



