import collaborator from './collaboratorStore'
import company from './companyStore'
import contact from './contactStore'
import importStore from './importStore'


const state = {
  ...collaborator.state,
  ...company.state,
  ...contact.state,
  ...importStore.state,
}

const getters = {
  ...collaborator.getters,
  ...company.getters,
  ...contact.getters,
  ...importStore.getters,
}

const mutations = {
  ...collaborator.mutations,
  ...company.mutations,
  ...contact.mutations,
  ...importStore.mutations,
}

const actions = {
  ...collaborator.actions,
  ...company.actions,
  ...contact.actions,
  ...importStore.actions,
}


export default {
  state,
  getters,
  mutations,
  actions,
}



