import CompanyCard from '@/components-app/CompanyCard'

export default {
  components: {
    CompanyCard,
  },
  props: ['invitations'],
  data() {
    return {
      companiesModels: [],
      loading: false,
    }
  },
  computed: {
    myCompanies() {
      return this.$store.getters.myCompanies
    },
  },
  methods: {
    accept(index) {
      const invitation = this.invitations[index]
      const company = myCompanies[index]
      // TODO: set the righ taction
      this.loadStore('rejectContactInvitation', {company, invitation})
    },
    reject(index) {
      const invitation = this.invitations[index]
      this.loadStore('rejectContactInvitation', invitation)
    },
    loadStore(action, payload) {
      this.loading = true
      this.$store.dispatch(action, payload)
        .finally(() => {
          this.loading = false
        })
    },
  },
}



