import importFPMMixin from '../../importFPMMixin'


export default {
  mixins: [importFPMMixin],
  computed: {
    records() {
      return this.$store.getters.unrecognizedCompanyRecords(this.value)
    },
  },
  methods: {
    load(newSet) {
      this.contractorName = newSet.company.name
      return this.$store.dispatch('loadUnrecognizedCompanyRecords', newSet)
    },
  },
}



