import FullPageModal from '@/components-app/modals/FullPageModal/index'
import dateUtils from '@/helpers/dateUtils'


export default {
  props: ['value'],
  components: {
    FullPageModal,
  },
  data() {
    return {
      contractorName: null,
      loading: false,
    }
  },
  computed: {
    records() {
      return this.$store.getters.unrecognizedCompanyRecords(this.value)
    },
    statementType() {
      return this.$store.getters.statementType
    },
    dates() {
      const importReceipt = this.$store.getters.importReceipt
      var dates = dateUtils.formatDate(importReceipt.toDate)
      if (importReceipt.fromDate) {
        dates = dateUtils.formatDate(importReceipt.fromDate) + ' - ' + dates
      }
      return dates
    },
    myCompany() {
      return this.$store.getters.selectedCompany
    },
  },
  watch: {
    value(newSet) {
      if (newSet) {
        this.contractorName = newSet.company.name
        this.$refs.modal.open()

        this.loading = true
        this.$store.dispatch('loadUnrecognizedCompanyRecords', newSet)
          .finally(() => {
            this.loading = false
          })
      }
    },
  },
  methods: {
    close() {
      this.$emit('input', null)
    },
  },
}



